
.lecturer-page {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    
}

/* Existing styles */
header {
    text-align: center;
    margin-bottom: 20px;
}

h1 {
    color: #FFD700;
}

p {
    color: #eee;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
}

th, td {
    padding: 10px;
    text-align: right;
    border: 1px solid #ddd;
    color: #eee;
}

th {
    background-color: #4CAF50;
    color: white;
}

button {
    padding: 5px 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

button:hover {
    background-color: #45a049;
}

a {
    color: #4CAF50;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

/* Modal Styles */
.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 500px;
    position: relative;
}

.close {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 28px;
    font-weight: bold;
    color: #aaa;
    cursor: pointer;
}

.close:hover,
.close:focus {
    color: black;
}

/* Responsive Styles */
@media only screen and (max-width: 600px) {
    table {
        font-size: 14px;
    }

    th, td {
        padding: 8px;
    }

    .modal-content {
        width: 90%;
    }
}


/* Definiše širinu za svaku kolonu u tabeli */
table th:nth-child(1),
table td:nth-child(1) {
    width: 20%; /* Možete prilagoditi ovu vrednost po potrebi */
}

table th:nth-child(2),
table td:nth-child(2) {
    width: 20%; /* Druga kolona */
}

table th:nth-child(3),
table td:nth-child(3) {
    width: 20%; /* Treća kolona */
}

/* Postavite širu širinu za poslednju kolonu (Naziv) */
table th:nth-child(4),
table td:nth-child(4) {
    width: 40%; /* Veća širina za kolonu "Naziv" */
}
